import React from 'react';
import './Header.scss';

const clickSingleA = (event) => {
  const items = document.getElementsByClassName(
    'navigation-link active-nav-link'
  );
  if (items.length) {
    items[0].className = 'navigation-link';
  }

  event.target.className = 'navigation-link active-nav-link';
};

const hamburgerClick = () => {
  console.log(document.getElementsByClassName('hamburger-menu'));
  const items = document.getElementsByClassName('ham hamRotate ham1 active');
  const menu = document.getElementsByClassName('hamburger-menu');
  if (items.length && menu.length) {
    items[0].removeAttribute('class');
    menu[0].removeAttribute('class');
    document.getElementById('ham').setAttribute('class', 'ham hamRotate ham1');
    document.getElementById('ham-menu').setAttribute('class', 'hamburger-menu');
  } else {
    document
      .getElementById('ham')
      .setAttribute('class', 'ham hamRotate ham1 active');
    document
      .getElementById('ham-menu')
      .setAttribute('class', 'hamburger-menu active-hamburger');
  }
};

